import React from "react";
import { Accordion, Card } from "react-bootstrap";
function PropertyHistory(): JSX.Element {
  return (
    <>
      <Accordion defaultActiveKey="0">
        <Card className="mt-5">
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <div className="card-header-inner ">
              <h6 className="content_sub_heading">Property History</h6>
              <span className="material-icons float-right arrow">
                keyboard_arrow_up
              </span>
            </div>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <div className="property-history">
                <div className="property-history-rectangle mb-2 ml-0 mr-0">
                  <div className="property-history-rectangle-left-content">
                    <p className="mb-0 property_history_date">june 9, 2020</p>
                    <p className="property_history_status mb-0 status-active">
                      Listed Active
                    </p>
                  </div>
                  <div className="property-history-rectangle-right-content">
                    <p className="mb-0">$565,630</p>
                  </div>
                  <div className="clearfix" />
                </div>

                <div className="property-history-rectangle  ml-0 mr-0">
                  <div className="property-history-rectangle-left-content">
                    <p className="mb-0 property_history_date">april 7, 2012</p>
                    <p className="property_history_status mb-0">Sold</p>
                  </div>
                  <div className="property-history-rectangle-right-content">
                    <p className="mb-0">$340,000</p>
                  </div>
                  <div className="clearfix" />
                </div>
              </div>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </>
  );
}
export default PropertyHistory;
