import React from "react";
import { Accordion, Card } from "react-bootstrap";
function Surrounding(): JSX.Element {
  return (
    <>
      <Accordion defaultActiveKey="0">
        <Card className="mt-5">
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <div className="card-header-inner ">
              <h6 className="content_sub_heading float-left w-50 p-0">
                Surrounding
              </h6>
              <span className="material-icons float-right arrow">
                keyboard_arrow_up
              </span>
            </div>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <div className="Surrounding">
                <div className="Surrounding_inner">
                  <p className="noise_level mb-0">Noise Level</p>
                  <p className="Surrounding_location">
                    N / A noise Level outside the property building at city
                  </p>
                </div>
              </div>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </>
  );
}
export default Surrounding;
