import React from "react";
import { Accordion, Card } from "react-bootstrap";
function PropertyTaxes(): JSX.Element {
  return (
    <>
      <Accordion defaultActiveKey="0">
        <Card className="mt-5">
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <div className="card-header-inner ">
              <h6 className="content_sub_heading m-0  float-left w-50 pb-0">
                Property Taxes
              </h6>
              <span className="material-icons float-right arrow">
                keyboard_arrow_up
              </span>
            </div>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <div className="property-taxes">
                <div className="row">
                  <div className="col-5">
                    <p className="mb-0">Tax Amount</p>
                  </div>
                  <div className="col-2">
                    <p className="mb-0">:</p>
                  </div>
                  <div className="col-5">
                    <p className="mb-0">$55630</p>
                  </div>
                  <div className="col-5">
                    <p className="mb-0">Land</p>
                  </div>
                  <div className="col-2">
                    <p className="mb-0">:</p>
                  </div>
                  <div className="col-5">
                    <p className="mb-0">$55630</p>
                  </div>
                  <div className="col-5">
                    <p className="mb-0">Improvements</p>
                  </div>
                  <div className="col-2">
                    <p className="mb-0">:</p>
                  </div>
                  <div className="col-5">
                    <p className="mb-0">$55630</p>
                  </div>
                  <div className="col-5">
                    <p className="mb-0">Total</p>
                  </div>
                  <div className="col-2">
                    <p className="mb-0">:</p>
                  </div>
                  <div className="col-5">
                    <p className="mb-0">$55630</p>
                  </div>
                </div>
              </div>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </>
  );
}
export default PropertyTaxes;
