import React from "react";
import { Accordion, Card } from "react-bootstrap";
function NeighbourHood(): JSX.Element {
  return (
    <>
      <Accordion defaultActiveKey="0">
        <Card className="mt-5">
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <div className="card-header-inner ">
              <h6 className="content_sub_heading float-left w-50 p-0">
                Neighborhood
              </h6>
              <span className="material-icons float-right arrow">
                keyboard_arrow_up
              </span>
            </div>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <div className="neighborhood">
                <p className="mb-0 neighborhood_p">Charlotte Hall, MD</p>
                <div className="row">
                  <div className="col-3">
                    <div className="neighborhood_content">
                      <p className="mb-0 neighborhood_price">$399k</p>
                      <p className="mb-0 neighborhood_detail">
                        Median Price for sale
                      </p>
                    </div>
                  </div>
                  <div className="col-3">
                    <div className="neighborhood_content">
                      <p className="mb-0 neighborhood_price">$150</p>
                      <p className="mb-0 neighborhood_detail">
                        Median Price per sq.ft
                      </p>
                    </div>
                  </div>
                  <div className="col-3">
                    <div className="neighborhood_content">
                      <p className="mb-0 neighborhood_price">48</p>
                      <p className="mb-0 neighborhood_detail">
                        Median Price on Market
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </>
  );
}
export default NeighbourHood;
