import React from "react";
import images from "../../assets/images";
function Header(): JSX.Element {
  return (
    <header>
      <div className="header main">
        <div className="row">
          <div className=" col-3 logo-sect p-0">
            <div className="logo">
              <img src={images.monogram} alt="Logo" />
            </div>
          </div>
          <div className="col-6 d-flex flex-column justify-content-center w-100 p-0">
            <div className="navbar">
              <nav>
                <ul>
                  <li>
                    <a href="#">Buy</a>
                  </li>
                  <li>
                    <a href="#">Sell</a>
                  </li>
                  <li>
                    <a href="#">Rent</a>
                  </li>
                  <li>
                    <a href="#">Loan</a>
                  </li>
                  <li>
                    <a href="#">The Experience</a>
                  </li>
                </ul>
              </nav>
            </div>
          </div>
          <div className="col-3 d-flex flex-column justify-content-center p-0">
            <div className="right-sect">
              <ul className="right-ul">
                <li>
                  <div className="dropdown">
                    <button
                      type="button"
                      className="btn dropdown-toggle language-toggle"
                      data-toggle="dropdown"
                    >
                      English
                    </button>
                    <div className="dropdown-menu">
                      <a className="dropdown-item" href="#">
                        French
                      </a>
                      <a className="dropdown-item" href="#">
                        Link 2
                      </a>
                      <a className="dropdown-item" href="#">
                        Link 3
                      </a>
                    </div>
                  </div>
                </li>
                <li>
                  <button className="btn login_btn">Login</button>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}
export default Header;
